import { Injectable, Injector, Type, inject } from '@angular/core';
import { EntityDetailsAdapter } from '../../models/entity-details-adapter.interface';
import { ENTITY_ADAPTER_REGISTRY } from './entity-adapter.token';
import { isDefined } from '../../../../shared/utils';

@Injectable({ providedIn: 'root' })
export class EntityAdapterFactory {
  private readonly adapters = new Map<string, EntityDetailsAdapter>();
  private readonly adapterTypes = new Map<string, Type<EntityDetailsAdapter>>();
  private readonly injector = inject(Injector);
  private readonly registrations = inject(ENTITY_ADAPTER_REGISTRY, {
    optional: true,
  });

  constructor() {
    const registrations = this.registrations;
    if (isDefined(registrations)) {
      for (const reg of registrations) {
        for (const type of reg.types) {
          this.adapterTypes.set(type.toLowerCase(), reg.adapter);
        }
      }
    }
  }

  /**
   * Gets the appropriate adapter for the given entity type.
   * Adapters are lazy-loaded and cached for performance.
   */
  getAdapter(entityType: string): EntityDetailsAdapter | null {
    const cacheKey = entityType.toLowerCase();

    // Return cached adapter if available
    if (this.adapters.has(cacheKey)) {
      const cachedAdapter = this.adapters.get(cacheKey);
      return cachedAdapter ?? null;
    }

    // Create and cache new adapter
    const adapter = this.createAdapter(entityType);
    if (adapter) {
      this.adapters.set(cacheKey, adapter);
    }

    return adapter;
  }

  /**
   * Creates the appropriate adapter instance for the given entity type
   */
  private createAdapter(entityType: string): EntityDetailsAdapter | null {
    const adapterType = this.adapterTypes.get(entityType.toLowerCase());

    if (!adapterType) {
      return null;
    }

    try {
      return this.injector.get(adapterType);
    } catch (error) {
      console.error(
        `Failed to create adapter for entity type: ${entityType}`,
        error,
      );
      return null;
    }
  }
}
